<template>
    <div class="user-message">
        <div class="com-title-bar">
            <div class="box">
                <div class="back" @click="$routerBack"></div>
                <div class="title">消息通知</div>
                <div class="action">
                    <div class="button"></div>
                </div>
            </div>
        </div>
        <div class="data">
            <div class="tabs">
                <van-tabs v-model="tabs.active" lazy-render swipeable animated sticky color="#FFEA03" :line-width="54" :line-height="2" :border="false">
                    <van-tab title="系统公告" name="system">
                        <div class="box">
                            <div class="empty" v-if="system.empty">
                                <img src="../../../assets/user/message/empty.png">
                                <div class="text">目前还没有消息哟</div>
                            </div>
                            <van-list v-model="system.loading" :finished="system.finished" finished-text="" @load="initSystem">
                                <div class="item" v-for="(item,index) in system.data" :key="index" @click="viewHandel('system',item.uuid)">
                                    <div class="x-title">
                                        <div class="status" :class="item.read?'read':''"></div>
                                        <div class="title">{{ item.title }}</div>
                                        <div class="date">{{ item.create_time }}</div>
                                    </div>
                                    <div class="abstract van-hairline--bottom">
                                        {{ item.abstract }}
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </van-tab>
                    <van-tab title="用户消息" name="user">

                        <div class="box">
                            <div class="empty" v-if="user.empty">
                                <img src="../../../assets/user/message/empty.png">
                                <div class="text">目前还没有消息哟</div>
                            </div>
                            <van-list v-model="user.loading" :finished="user.finished" finished-text="" @load="initUser">
                                <div class="item" v-for="(item,index) in user.data" :key="index" @click="viewHandel('user',item.uuid)">
                                    <div class="x-title">
                                        <div class="status" :class="item.read?'read':''"></div>
                                        <div class="title">{{ item.title }}</div>
                                        <div class="date">{{ item.create_time }}</div>
                                    </div>
                                    <div class="abstract van-hairline--bottom">
                                        {{ item.abstract }}
                                    </div>
                                </div>
                            </van-list>
                        </div>
                    </van-tab>
                </van-tabs>
            </div>
        </div>
    </div>
</template>

<script>
    import {Tab, Tabs, List} from 'vant';

    export default {
        components: {
            [Tab.name]: Tab,
            [Tabs.name]: Tabs,
            [List.name]: List
        },
        data() {
            return {
                tabs: {
                    active: 'system'
                },
                system: {
                    data: [],
                    loading: false,
                    finished: false,
                    empty: false,
                    page: 1
                },
                user: {
                    data: [],
                    loading: false,
                    finished: false,
                    empty: false,
                    page: 1
                }
            }
        },
        mounted() {
        },
        methods: {
            initSystem() {
                this.$axios.get('/api/app/system/notice/data', {
                    page: this.system.page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.system.page++;
                            if (this.system.page > result.last_page) {
                                this.system.finished = true;
                            }
                            this.system.data = this.system.data.concat(result.data);
                            if (this.system.data.length === 0) {
                                this.system.empty = true;
                            }
                            this.system.loading = false;
                        }
                    })
            },
            initUser() {
                this.$axios.get('/api/app/user/message/data', {
                    page: this.user.page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.user.page++;
                            if (this.user.page > result.last_page) {
                                this.user.finished = true;
                            }
                            this.user.data = this.user.data.concat(result.data);
                            if (this.user.data.length === 0) {
                                this.user.empty = true;
                            }
                            this.user.loading = false;
                        }
                    })
            },
            viewHandel(type, uuid) {
                this.$router.push('/user/message/store?type=' + type + '&uuid=' + uuid);
            }
        }
    }
</script>

